@font-face {
  font-family: 'StateFaceRegular';
  src: url('../fonts/stateface-regular-webfont.eot');
  src: url('../fonts/stateface-regular-webfont.eot?#iefix') format('embedded-opentype'),
        url('../fonts/stateface-regular-webfont.woff') format('woff'),
        url('../fonts/stateface-regular-webfont.ttf') format('truetype'),
        url('../fonts/stateface-regular-webfont.svg#StateFaceRegular') format('svg');
  font-weight: normal;
  font-style: normal;
}
.stateface:before {
  font-family: StateFaceRegular;
  margin-right: 5px;
}

.stateface-replace {
  text-indent: -999em;
  position: relative;
  min-width: 1em;
  min-height: 1em;
  display: block;
  font-size: 4rem;
  line-height: 4rem;
  margin-right: 1rem;
}

.stateface-replace:before {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  text-indent: 0;
}

